html {
  height: 100%;
}

body {
  background: linear-gradient(#141e30, #243b55);
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.form {
  max-width: 400px;
  flex-direction: column;
  margin-top: 20px;
  display: flex;
}

.label_text {
  color: #fff;
  margin-bottom: 15px;
}

.little_text {
  color: #fff;
  margin-right: 55px;
}

input[type="number"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px;
  font-size: 14px;
}

.user_button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(270deg, rgba(0, 0, 0, 0), #03e9f4);
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

.user_button:hover {
  color: silver;
}

p {
  margin-bottom: 10px;
}

